@use '../util' as *;

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: rem(30);

  @include breakpoint(medium) {
    grid-template-columns: repeat(2, 1fr);
  }

  @include breakpoint(large) {
    grid-template-columns: repeat(4, 1fr);
  }
}


.card {
    background: var(--dark-card);
    color: var(--dark-text1);
    flex: 1; 
    text-align: center;
    border-radius: rem(5);
    padding: rem(25);
    overflow: hidden;

    &--facebook {
        border-top: rem(5) solid var(--facebook);
      }
    
      &--twitter {
        border-top: rem(5) solid var(--twitter);
      }
    
      &--instagram {
        position: relative;
        &::before {
            content: '';
            height: rem(5);
            width: 100%;
            background: var(--instagram-start);
            position: absolute;
            display: block;
            left: 0;
            top: 0;

            background: linear-gradient(
              225deg,
              var(--instagram-end),
              var(--instagram-middle) 50.91%,
              var(--instagram-start) 100%
            );
        }
      }
    
      &--youtube {
        border-top: rem(5) solid var(--youtube);
      }


    &__icon {
        margin-right: rem(8);
        &--facebook{

        }
        &--twitter{

        }
        &--instagram{

        }
        &--youtube{

        }
    }

    &__platform {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: rem(28);
        margin-top: rem(5);
    }

    &__username {
        font-size: rem(12);
        font-weight: bold;
    }

    &__count {
        color: var(--light-bg);
        font-weight: 700;
        letter-spacing: rem(-2);
        line-height:1;
        margin-bottom: rem(4);

        &--big {
            font-size: rem(56);
        }
    }

    &__followers {
        margin-bottom: rem(25);
    }

    &__label {
        font-size: rem(12);
        letter-spacing: rem(5);
        font-weight: 400;
        color: var(--dark-text1);
        text-transform: uppercase;
    }

    &__change {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: rem(12);
        font-weight: 700;

        &--up {
            color: var(--Limegreen);
        }

        &--down {
            color: var(--brightred);
        }
    }

    img {
        margin-right: rem(4);
    }

    &__number {
       
    }
}

